
import qs from 'qs'
import { useLocation, useHistory } from 'react-router-dom'
import React, { useState, useEffect } from 'react'; 

const tryParseInt = value => {
  const result = parseInt(value)
  return isNaN(result) ? value : result
}

const parseObjectValues = (obj = {}) => {
  Object.keys(obj).forEach(k => {
    obj[k] = tryParseInt(obj[k])
  })

  return obj
}

const useQuery = () => {
  const location = useLocation()
  const history = useHistory()

  const value = parseObjectValues(
    qs.parse(location.search, { ignoreQueryPrefix: true }) || {}
  )

  const setQueryInner  = params => history.push({
    pathname: location.pathname,
    search: qs.stringify({ ...value, ...parseObjectValues(params) })
  })

  const [query,setQuery] = useState({value,set: setQueryInner });
//   console.log(`location init  ${JSON.stringify(location.search)}`)
  useEffect(() => {
    // wrapper location to query always trigger twice 
    var obj = {value,set: setQueryInner }
    setQuery(old => obj)
    console.log("location")
  }, [location])
  
  return query
}

export default useQuery